<template>
  <div id="app">
    <h1>Monsters Rolodex</h1>
    <SearchBox v-model="searchField" type="search" placeholder="search monsters" />
    <CardList :monsters="filterdMonsters" />
  </div>
</template>

<script>
import CardList from "@/components/card-list/card-list.component.vue";
import SearchBox from "@/components/search-box/search-box.component.vue";
export default {
  components: {
    CardList,
    SearchBox,
  },
  data() {
    return {
      monsters: [],
      searchField: "",
    };
  },
  created() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((data) => {
        this.monsters = data;
      });
  },
  computed: {
    filterdMonsters() {
      return this.monsters.filter((monster) =>
        monster.name.toLowerCase().includes(this.searchField.toLowerCase())
      );
    },
  },
};
</script>

